* {
  box-sizing: border-box;
}

svg {
  width: 100%;
  height: 100%;
  display: block;
  fill: currentColor;
}

body {
  position: relative;
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
}

html,
body {
  height: 100%;
}

button,
textarea {
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 0;
  background-color: transparent;
  color: inherit;
  box-shadow: none;
  font: inherit;
  letter-spacing: inherit;
}

button {
  cursor: pointer;
}

a {
  color: inherit;
  text-decoration: none;
}

.extension {
  min-width: 500px;
}

.export-icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px 15px 15px;
}
.export-icons__workspace {
  width: 100%;
}
.export-icons__result {
  margin-top: 50px;
}
.export-icons__version {
  position: absolute;
  top: 15px;
  right: 15px;
  color: #B5B5B5;
}

.export-icons-workspace {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.export-icons-workspace__textarea {
  width: 100%;
  padding: 15px;
  resize: none;
  border: 1px solid #00375E;
  border-radius: 4px;
  transition: border-color 0.2s ease-in-out;
}
.export-icons-workspace__textarea:focus {
  outline: none;
  border-color: #0064ab;
}
.export-icons-workspace__buttons {
  width: 100%;
  margin-top: 25px;
  display: flex;
  justify-content: space-between;
}

.button {
  max-width: 100%;
  min-height: 40px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 15px 30px;
  text-align: center;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border 0.2s ease-in-out;
}
.button--size-s {
  min-height: 30px;
  padding: 5px 10px;
}
.button--is-rounded {
  border-radius: 40px;
}
.button--icon-only {
  padding: 10px;
}
.button--icon-only .button__icon {
  width: 50px;
  height: 50px;
}
.button--icon-only .button__icon .icon__svg {
  fill: #FFF;
}
.button__container {
  flex: auto 0 1;
  min-width: 0;
  white-space: nowrap;
}
.button__label {
  display: block;
  overflow: hidden;
  color: #FFF;
  text-overflow: ellipsis;
}
.button--theme-primary {
  background-color: #276AA5;
}
.button--theme-primary:hover {
  background-color: #1F5584;
}
.button--theme-secondary {
  background-color: #25A248;
}
.button--theme-secondary:hover {
  background-color: #229643;
}

.export-icons-table {
  margin: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  padding: 0;
  list-style: none;
}
.export-icons-table__cell {
  display: flex;
  align-items: center;
  padding: 15px;
}

.icon-single__icon {
  width: 150px;
  height: 150px;
  display: block;
  padding: 15px;
  background: url("../images/icon_bg.jpg") no-repeat center/cover;
}
.icon-single__icon .icon__svg {
  fill: #FFF;
}
.icon-single__actions {
  display: flex;
  align-items: center;
}
.icon-single__action {
  flex: 1 1 50%;
  max-width: 50%;
}

.icon {
  display: flex;
  align-items: center;
  justify-content: center;
}

.export-icons-sprites__item + .export-icons-sprites__item {
  margin-top: 10px;
}
.export-icons-sprites__link {
  color: #1F5584;
}

.export-icons-sprites-link {
  display: flex;
  flex-direction: column;
}
.export-icons-sprites-link__url {
  text-decoration: underline;
}